import React from "react";
import { Button, Container, Grid, Typography } from "@mui/material";

export default function Acerca() {
  return (
    <Container maxWidth="sm">
      <Grid
        container
        spacing={3}
        direction="column"
        alignItems="center"
        justifyContent="center"
      >
        <Grid item xs={8}>
          <Typography variant="h3" component="div">
            Acerca de WeasUTP
          </Typography>
          <Typography variant="body1" component="div">
            Hola! WeasUTP es un proyecto personal creado por m,{" "}
            <b>Sebastin Morales</b> al principio con la intencin de poder
            estudiar de una manera ms entretenida y didctica las matemticas
            informticas.
            <br />
            <br />
            Las herramientas disponibles en la pgina resuelven problemas
            especificos y simplifican el proceso de algoritmo de manera que
            puedes visualizar la respuesta directamente.
            <br />
            <br />
            <b>La pgina se encuentra en construccin!</b> la experiencia de usuario no es muy agradable por ahora
            y puede que te encuentres un par de errores o diseos sin terminar.
          </Typography>
        </Grid>
      </Grid>
    </Container>
  );
}
